#root {
  margin: 0 auto;
  width: 80%;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

#row_players {
  display: flex;
  justify-content: space-evenly;
}

.box {
  border: 1px solid black;
}

/*--------- Board stuff ---------*/
#board_div_category {
  background-color: blue;
  color: white;
  text-align: center;
  width: 100%;
  font-size: 24pt;
}

#board_grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.board-grid-row {
  display: flex;
}

.board-box {
  border: 2px solid black;
  background-color: mediumseagreen;
  height: 4rem;
  width: 3rem;

  font-size: 2.5rem;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
}

.board-box-hidden {
  border: 2px solid none;
  background-color: none;
  border-radius: 3px;
  height: 3rem;
  width: 3rem;
}

/*--------- Player stuff ---------*/
.player-list {
  display: flex;
  gap: 2em;
}

.player-card {
  background-color: darkblue;
  border-radius: 4px;
  color: lightblue;
  padding: 1em;

  width: 8em;
  height: 5em;
}

.player-card-isturn {
  background-color: blue;
  border-radius: 4px;
  color: white;
  padding: 1em;

  width: 8em;
  height: 5em;
}

.player-card-bankrupt {
  background-color: gray;
  border-radius: 4px;
  color: white;
  padding: 1em;

  width: 8em;
  height: 5em;
}

/* Rearrange the design display */
.main_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 60px;
}
.main_title {
  font-size: 45px;
  margin-bottom: 18px;
}
.play-regular {
  font-family: "Play", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: x-large;
}

.play-bold {
  font-family: "Play", sans-serif;
  font-weight: 700;
  font-style: normal;
}
.play-board {
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 40px;
}
/*--------- ModalComponent styling ---------*/
.modalcomponent-background-div {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modalcomponent-card-div {
  background-color: #fff;
  padding: 2rem;
  border-radius: 10px;
  text-align: center;
  width: 300px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  position: relative;
}

.modalcomponent-button {
  margin-top: 0.5rem;
  padding: 0.5rem 1rem;
  display: block;
  margin-left: auto;
  margin-right: auto;
  background-color: #ccc;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
